<template>
	<view class="screen">
		<!-- 背景图片 -->
		<image 
			class="bg-image-1" 
			:src="currentHeaderBg" 
			mode="aspectFill"
		></image>
		<image 
			v-if="currentMainBg"
			class="bg-image-2" 
			:src="currentMainBg" 
			mode="aspectFill"
		></image>
		
		<!-- 状态栏和头部 -->
		<view class="header-wrapper">
			<!-- 导航栏 -->
			<view class="nav-bar">
				<view class="nav-left">
					<view class="back-btn" @tap="goBack">
						<image class="back-icon" src="https://c.animaapp.com/mi4wi1dxPPrFZt/img/frame-4.svg" mode="aspectFit"></image>
					</view>
					<text class="nav-title">美发</text>
				</view>
				<image class="logo-group" src="https://c.animaapp.com/mi4wi1dxPPrFZt/img/group-8.png" mode="aspectFit"></image>
			</view>
			
			<!-- 标签选择 -->
			<view class="badge-container">
				<view class="category-badge">
					<image class="badge-icon" :src="currentBadgeIcon" mode="aspectFit"></image>
					<text class="badge-text">设计师</text>
				</view>
			</view>
		</view>
		
		<!-- Tabs -->
		<view class="tabs-wrapper">
			<view class="tabs-list">
				<view 
					v-for="(tab, index) in tabItems" 
					:key="index"
					class="tab-item"
					:class="{ active: activeTab === tab.value }"
					@tap="switchTab(tab.value)"
				>
					<text class="tab-label" :class="{ active: activeTab === tab.value }">{{ tab.label }}</text>
					<image 
						v-if="activeTab === tab.value"
						class="tab-indicator" 
						src="https://c.animaapp.com/mi4wi1dxPPrFZt/img/rectangle-215.svg" 
						mode="aspectFit"
					></image>
				</view>
			</view>
		</view>
		
		<!-- 主内容 -->
		<view class="main-content">
			<!-- 设计师板块 -->
			<template v-if="activeTab === 'designer'">
				<design-section></design-section>
				<service-options-section></service-options-section>
			</template>

			<!-- 优服务板块 -->
			<template v-if="activeTab === 'service'">
				<featured-services-section></featured-services-section>
				<recommendations-section></recommendations-section>
				<vip-section></vip-section>
				<service-gallery-section></service-gallery-section>
			</template>

			<!-- 品牌馆板块 -->
			<template v-if="activeTab === 'brand'">
				<view class="brand-content">
					<view class="featured-section animate-fade-up">
						<brand-featured-items-section></brand-featured-items-section>
					</view>
					<view class="item-list-section-wrapper animate-fade-up">
						<brand-item-list-section></brand-item-list-section>
					</view>
				</view>
			</template>
		</view>
		
		<!-- 底部指示器 -->
		<view class="footer">
			<view class="indicator-bar"></view>
		</view>
	</view>
</template>

<script>
import DesignSection from '../../components/DesignSection.vue'
import ServiceOptionsSection from '../../components/ServiceOptionsSection.vue'
import FeaturedServicesSection from '../../components/FeaturedServicesSection.vue'
import RecommendationsSection from '../../components/RecommendationsSection.vue'
import VIPSection from '../../components/VIPSection.vue'
import ServiceGallerySection from '../../components/ServiceGallerySection.vue'
import BrandFeaturedItemsSection from '../../components/BrandFeaturedItemsSection.vue'
import BrandItemListSection from '../../components/BrandItemListSection.vue'

export default {
	components: {
		DesignSection,
		ServiceOptionsSection,
		FeaturedServicesSection,
		RecommendationsSection,
		VIPSection,
		ServiceGallerySection,
		BrandFeaturedItemsSection,
		BrandItemListSection
	},
	data() {
		return {
			activeTab: 'designer',
			tabItems: [
				{ value: "designer", label: "设计师" },
				{ value: "service", label: "优服务" },
				{ value: "brand", label: "品牌馆" },
			],
			// Assets configuration
			assets: {
				designer: {
					headerBg: "https://c.animaapp.com/mi4wi1dxPPrFZt/img/rectangle-217.png",
					mainBg: "https://c.animaapp.com/mi4wi1dxPPrFZt/img/group-33.png",
					badgeIcon: "https://c.animaapp.com/mi4wi1dxPPrFZt/img/frame-1.svg"
				},
				service: {
					headerBg: "https://c.animaapp.com/mi5bcgvrGbkedE/img/rectangle-217.png",
					mainBg: null,
					badgeIcon: "https://c.animaapp.com/mi5bcgvrGbkedE/img/frame-3.svg"
				},
				brand: {
					headerBg: "https://c.animaapp.com/mi5cgxi6ndVkfo/img/rectangle-217.png",
					mainBg: "https://c.animaapp.com/mi5cgxi6ndVkfo/img/group-33.png",
					badgeIcon: "https://c.animaapp.com/mi5cgxi6ndVkfo/img/frame-4.svg"
				}
			}
		}
	},
	computed: {
		currentHeaderBg() {
			return this.assets[this.activeTab].headerBg
		},
		currentMainBg() {
			return this.assets[this.activeTab].mainBg
		},
		currentBadgeIcon() {
			return this.assets[this.activeTab].badgeIcon
		}
	},
	onLoad(options) {
		if (options.tab) {
			// Ensure the tab exists
			const validTabs = ['designer', 'service', 'brand'];
			if (validTabs.includes(options.tab)) {
				this.activeTab = options.tab;
			}
		}
	},
	methods: {
		goBack() {
			uni.navigateBack()
		},
		switchTab(value) {
			this.activeTab = value
		}
	}
}
</script>

